import http from '../http';

const search = {
            /**
         * 
         * keyword 
         * Size (1-101010)
         */
        suggestion(queryFilters){
            return http.get(`js/v7/search/Suggestion`, { params: queryFilters });
        },

                /**
         * 
         */
        query(queryFilters) {
            return http.get(`js/v7/search/SearchAll`, { params: queryFilters });
        },

        /**
         * 
         */
        hots(){
            return http.get(`js/v7/search/HotKeywords`);
        },
        /**
         * 
         */
        areas() {
            return http.get(`js/v7/area/getlist`);
        },
        /**
         * 
         */
        projects(){
            return http.get(`js/v7/project/getlist`);
        },
        /**
         *  
         */
        prod(queryFilters){
            return http.get(`js/v7/search/ProductSearch`, { params: queryFilters });
        },
        /**
         *  
         */
        hospital(queryFilters) {
            return http.get(`Js/v7/search/HospitalSearch`, { params: queryFilters });
        }, 
        /**
         * 
         */
        doctor(queryFilters){
            return http.get(`Js/v7/search/DoctorSearch`, { params: queryFilters });
        },               
        /**
         * 
         */
        diary(queryFilters){
            return http.get(`Js/v7/search/DiarySearch`, { params: queryFilters });
        },
        /**
         * 
         */
        article(queryFilters) {
            return http.get(`Js/v7/search/ArticleSearch`, { params: queryFilters });
        },
        /**
         * 
         */
        users(queryFilters){
            return http.get(`Js/v7/search/UserSearch`, { params: queryFilters });
        },
        /**
         * 
         */
        tags(queryFilters){
            return http.get(`Js/v7/search/TagSearch`, { params: queryFilters });
        },
        /**
         * 
         */
        follow(queryFilters){
            return http.post(`Js/v7/user/clickfollow`, queryFilters);
        }                                 
        
}

export default search;


