<template>
    <header class="m-header">
        <div class="m-header-button header-is-left">
            <slot name="left"></slot>
        </div>
        <div class="m-header-title">
            <slot></slot>
        </div>
        <div class="m-header-button header-is-right">
            <slot name="right"></slot>
        </div>
    </header>
</template>
<script>
/**
 * m-header
 * @module components/mContainer
 * @desc 
 *
 * @example
    <m-header>
        <div slot="left"></div>
        <h1 slot="title" class="m-header-title"></h1>
        <div slot="right"></div> 
    </m-header>
 */
</script>


